<template>
  <div class="card">
    <img v-bind:src="plantImageSrc" v-bind:alt="plant.name" class="card-img" />
    <div class="card-body">
      <h4 class="card-title">{{ plant.name }}</h4>
      <p class="card-text">{{ plant.genus }}</p>
      <p class="card-text">Pot Size: {{ plant.potSize }}</p>
      <p class="card-text">Price: ${{ plant.price }}</p>
    </div>
    <div class="form">
      <div class="quantity-component">
        <label for="quantity">Quantity:</label>
        <div class="quantity-selector">
          <button class="button" v-on:click="decrement(1)">-</button>
          <input
            type="text"
            name="quantity"
            v-model="quantity"
            class="input-field"
          />
          <button class="button" v-on:click="quantity += 1">+</button>
        </div>
      </div>

      <button v-on:click="addPlantToCart" class="card-button">
        {{ cartFeedback }}
      </button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { mapMutations, mapActions } from "vuex";

export default {
  props: ["plant"],
  data() {
    return {
      cartFeedback: "Add To Cart",
      quantity: 0,
      plantImage: null
    };
  },
  watch: {
    quantity: function() {
      if (this.quantity > 0) {
        this.cartFeedback = "Add To Cart";
      }
    }
  },
  computed: {
    plantImageSrc() {
      return `data:image/jpeg;base64, ${this.plantImage}`;
    }
  },
  methods: {
    ...mapMutations(["addToCart", "updateCartQuantity"]),
    ...mapActions(["getPlantImage"]),
    addPlantToCart() {
      this.addToCart({ plant: this.plant, quantity: this.quantity });
      this.updateCartQuantity();
      this.cartFeedback = `${this.quantity} Added To Cart!`;
      this.quantity = 0;
    },
    async getImage() {
      let res;
      if (this.plant.images[0]) {
        res = await axios.get(
          `/api/v1/plants/images/${this.plant.images[0].filename}`,
          { responseType: "arraybuffer" }
        );
      } else {
        res = await axios.get(
          `/api/v1/plants/images/image-coming-soon-placeholder.png`,
          { responseType: "arraybuffer" }
        );
      }
      this.plantImage = Buffer.from(res.data, "binary").toString("base64");
    },
    decrement(amount) {
      if (this.quantity - amount < 0) {
        this.quantity = 0;
      } else {
        this.quantity -= amount;
      }
    }
  },
  created() {
    this.getImage();
  }
};
</script>

