<template>
  <div class="plants-view">
    <PlantCard
      v-for="plant in plants"
      v-bind:key="plant._id"
      v-bind:plant="plant"
      class="card-plant"
    />
  </div>
</template>

<script>
import PlantCard from "@/components/PlantCard";
import { mapGetters, mapActions } from "vuex";

export default {
  components: {
    PlantCard
  },
  data() {
    return {};
  },
  computed: {
    ...mapGetters(["plants"])
  },
  methods: {
    ...mapActions(["getPlants"]),
    async getPlantsResult() {
      if (this.$route.params.genus === "all") {
        await this.getPlants({});
      } else {
        await this.getPlants(this.$route.params);
      }
    }
  },
  watch: {
    "$route.params": async function() {
      await this.getPlantsResult();
    }
  },
  async created() {
    await this.getPlantsResult();
  }
};
</script>

<style scoped>
.plants-view {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin: 5px;
}
.card-plant {
  margin: 5px;
}
</style>